.gallery_grid {
    margin-top: 20px;
    margin-bottom: 20px;
    width: 75%;
    max-width: 1080px;
    display: grid;
    gap: 1em;
    grid-template-columns: repeat(3, 1fr);
  }

.category_desc {
  font-size: larger;
  color: #FFE9B1;
  margin-top: 20px;
  margin-bottom: 20px;
  display: inline;
}

h3 {
  text-align: left;
  display: inline;
}

  /* Extra large devices (large laptops and desktops, 1200px and up) */
  @media only screen and (max-width: 1100px) {
    .gallery_grid {
      width: 70%;
      margin-left: 1%;
      max-width: 890px;
      grid-template-columns: 50% 50%;
    }

  } 
  @media only screen and (max-width: 890px) {
    .gallery_grid {  
      width: 95%;
    }
  }
  
  @media only screen and (max-width: 768px) {
    .gallery_grid {
         width: 95%;
         margin-left: 1%;
     }
  }

  @media only screen and (max-width: 600px) {
    .gallery_grid {
        width: 100%;
        margin-left: 0;
        grid-template-columns: 100%;
    }

    img {
        width: 100%;
        padding: 0 0 20px 0;
    }
  }
  