.imageinfo-main {
    width: 50%;
    margin: 0 auto;
}

h1, h2, h3, p {
    color: #FFE9B1;
    margin-bottom: 20px;
}

h2 {
    text-align: center;
}


.imageinfo-main p {
    text-align: justify;
    font-size: larger;
}

.imageinfo-main img {
    display: block;
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    border: 5px solid #332A1F;
    border-radius: 10px;
}

.imageinfo-main .min {
    width: 200px;
    display: inline;
    margin-top: 20px;
}

.imageinfo-main .full {
    width: 60%;
    display: inline;
    margin-top: 20px;
}

a {
    color: #FFE9B1;
}

.imageinfo-main a {
    text-decoration: underline;
}

@media only screen and (max-width: 900px) {
    .imageinfo-main img {
        width: 95%;
    }
    .imageinfo-main {
        width: 95%;
    }
  }

@media only screen and (max-width: 600px) {
    .imageinfo-main img {
        padding-bottom: 0px;
    }
  }