nav {
    width: 20%;
    min-width: 250px;
    float: left;
    margin-right: 10px;
    padding-top: 2em;
    padding-bottom: 2em;
}

ul {
    display: block;
}

ul ul {
    display: block;
}

li {
    list-style: inside;
    padding-left: 20px;
    margin-top: 10px;
    font-size: 1.5em;
}

li li {
    font-size: smaller;
}

li>a, button {
    color: #FFE9B1;
}

button {
    font-weight: bold;
    display: none;
    width: 100%;
    height: 2em;
    border: 1px solid #FFE9B1;
    background-color: #292e35;
}

@media only screen and (max-width: 890px) {
    nav {
        width: 100%;
    }
    ul {
        display: none;
    }
    li {
        margin-top: 5px;
        font-size: 1.5em;
    }
    button {
        display: initial;
    }
  }
