.gallery_item {
  border: 1px solid #000;
  background-color: #0B0C10;
  padding: 20px;
  border-radius: 10px;
  height: 100%;
}

.gallery_item:hover {
  border: 2px solid #332A1F;
}

img {
  width: 100%;
  margin-bottom: 10px;
}

.item_title {
    text-align: center;
    padding-bottom: 10px;
    font-weight: bold;
    font-size: larger;
    color: #FFE9B1;
  }
