.radio {
    display: inline-block;
    width: 75%;
}

hr {
    margin-top: 2em;
    margin-bottom: 2em;
}

.radio_div {
    margin: 0 auto;
    width: 90%;
    padding: 0;
}

.radio_div img {
    margin: 0;
    padding: 20px;
    width: 33%;
}

@media only screen and (max-width: 1100px) {
    .radio {
        width: 70%;
    }
    
    .radio_div img {
        width: 50%;
    }
  } 


@media only screen and (max-width: 890px) {
    .radio {
        width: 100%;
    }
}

@media only screen and (max-width: 600px) {
    .radio_div img {
        margin: 0;
        padding: 20px;
        width: 100%;
    }
}